import "date-fns";
import DateFnsUtils from "@date-io/date-fns";
import * as Yup from "yup";
import axios from "../../axios";
import React, { Component } from "react";
import { Formik, Form, Field } from "formik";
import {
  Button,
  LinearProgress,
  Grid,
  FormControl,
  InputLabel,
  MenuItem,
} from "@material-ui/core";
import { TextField, Select } from "formik-material-ui";
import { connect } from "react-redux";

import { DateTimePicker, MuiPickersUtilsProvider } from "@material-ui/pickers";
import { now } from "moment";

const mapStateToProps = (state) => ({
  collegeID: state.collegeID,
});
class NewEventPhoto extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedDate: new Date(now()),
      file: [],
      eventList: [],
      selectedEventCat: "",
    };
  }
  handleDateChange(date) {
    this.setState({
      selectedDate: new Date(date).toISOString(),
    });
  }
  componentDidMount() {
    axios
      .get(`api/events/` + this.props.collegeID, {})
      .then((res) => {
        this.setState({
          eventList: res.data,
        });
      })
      .catch((error) => {
        console.log(error);
      });
  }

  render() {
    const handleChangeEventCat = (event) => {
      let id = event.target.value;
      this.setState({
        selectedEventCat: id,
      });
    };

    async function uploadFile(file1, callback) {
      let bodyFormData = new FormData();
      bodyFormData.append("file", file1);
      const request = axios
        .post("upload", bodyFormData, {
          headers: {
            // Overwrite Axios's automatically set Content-Type
            "Content-Type": "multipart/form-data",
          },
        })
        .then((res) => {
          //alert(res.data.fileName)
          let filena = res.data.fileName;
          request.then(() => callback(filena));
          request.catch((err) => callback(err.response));
        });
    }

    const fileSelected = (event) => {
      this.setState({
        file: event.target.files[0],
      });
    };
    return (
      <Grid spacing={2} lg={12} xs={12}>
        <Grid item lg={12} xs={12}>
          <h1 class="card-title">Add Event Photo(s) </h1>
        </Grid>

        <Formik
          initialValues={{
            event_id: "",
            photo_link: "",
          }}
          onSubmit={(values, { setSubmitting, resetForm }) => {
            let fn = "";
            uploadFile(this.state.file, (response) => {
              // your code after API response
              fn = response;
              setTimeout(() => {
                setSubmitting(false);
                values.event_id = this.state.selectedEventCat;
                values.photo_link = fn;
                axios
                  .post("api/addeventphoto/" + this.props.collegeID, values)
                  .then((res) => {
                    if (res.data.flag === "success") {
                      alert("The Event Photo Added Successfully");
                    }
                  })
                  .catch((error) => {
                    alert(error);
                    resetForm();
                  });
                resetForm();
                this.setState({
                  file: [],
                });
              }, 500);
            });
          }}
        >
          {({ submitForm, isSubmitting }) => (
            <Form>
              <Grid item lg={12} md={6}>
                <div class="formspacer " />
                <FormControl variant="outlined" fullWidth required>
                  <InputLabel htmlFor="selectedEventCat">
                    Select Event
                  </InputLabel>
                  <Field
                    component={Select}
                    name="selectedEventCat"
                    label="Select Event Catagory"
                    onChange={handleChangeEventCat}
                  >
                    {this.state.eventList.map((event) => (
                      <MenuItem key={event.id} value={event.id}>
                        {event.event_name}
                      </MenuItem>
                    ))}
                  </Field>
                </FormControl>
              </Grid>

              <Grid item lg={12} xs={12}>
                <div class="formspacer " />
                <span
                  style={{
                    color: "#fff",
                    padding: "9px",
                    backgroundColor: "#b00",
                    marginRight: "20px",
                  }}
                >
                  Choose Event Photo in JPG/PNG/GIF format
                </span>

                <input
                  onChange={fileSelected}
                  type="file"
                  accept="image/png, image/gif, image/jpeg"
                ></input>
              </Grid>

              <Grid item lg={12} xs={12}>
                <div class="formspacer " />
                {isSubmitting && <LinearProgress />}
                <div class="formspacer " />
                <Button
                  variant="contained"
                  color="primary"
                  disabled={isSubmitting}
                  onClick={submitForm}
                  fullWidth
                >
                  Add New Event Photo
                </Button>
              </Grid>
            </Form>
          )}
        </Formik>
      </Grid>
    );
  }
}

export default connect(mapStateToProps)(NewEventPhoto);
